@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900&display=swap%27);
* {
  margin: 0px;
  padding: 0px;
}

:root {
  --color-1: #011627;
  --color-2: #112240;
  --color-3: #a1e4f1;
  --color-4: #b4e9f4;
  --color-5: #f72585;
}

body {
  background-color: #011627;
  background-color: var(--color-1);
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

#root {
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

/* fade in classes */
.fade-in {
  opacity: 0;
  transition: opacity 1000ms ease-in;
}

.fade-in.appear {
  opacity: 1;
}

/* flex box classes */
.flex {
  display: flex;
}

.flexColumn {
  display: flex;
  flex-direction: column;
}

.flexJustifyAndAlignCenter {
  justify-content: center;
  align-items: center;
}

.flexAlignCenter {
  align-items: center;
}

.flexJustifyCenter {
  justify-content: center;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: #112240;
  background-color: var(--color-2);
}

.HTMLTags {
  font-family: Montserrat;
  font-size: 4.5vh;
  color: #f72585;
  color: var(--color-5);
  font-weight: 400;
  margin: 1vh 0px;
}

@media (max-width: 1050px) {
  .HTMLTags {
    font-size: 25px;
  }
}


#firstPage {
  position: relative;
  left: 0px;
  top: 0px;
  height: 100vh;
  width: 100vw;
}

#introContainer {
  position: absolute;
  top: 20%;
  left: 10%;
  width: 80%;
  height: 60%;
  font-family: Montserrat;
  grid-gap: 6vh;
  gap: 6vh;
}

.introText {
  width: 65%;
  height: 100%;
  justify-content: center;
}

#introContainer h2 {
  margin-top: 1.65vh;
  margin-left: 3.25vh;
  font-size: 6.5vh;
  color: var(--color-4);
}

#introContainer h3 {
  margin-bottom: 1.65vh;
  margin-left: 3.25vh;
  font-size: 7.5vh;
  color: var(--color-3);
}

#introContainer p {
  margin: 3.25vh 0px;
  color: var(--color-3);
  margin-left: 3.25vh;
  font-size: 4vh;
}

.introImageDiv {
  width: 35%;
  height: 100%;
}

.introImageDiv img {
  border-radius: 50%;
  width: 100%;
  aspect-ratio: 1;
}

@media (max-width: 1050px) {
  #firstPage {
    position: relative;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 1px 0px;
  }

  #introContainer {
    position: relative;
    flex-direction: column;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    grid-gap: 10px;
    gap: 10px;
    align-items: center;
    margin: 80px 0px;
    margin-right: 0px;
  }

  #introContainer h2 {
    font-size: 4vh;
    margin: 0px 3%;
  }

  #introContainer h3 {
    font-size: 4.5vh;
    margin: 0px 3%;
  }

  #introContainer p {
    font-size: 2.5vh;
    margin: 2% 3%;
    margin-bottom: 0;
  }

  .introText {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 95%;
    order: 2;
  }

  .introImageDiv {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    order: 1;
  }

  .introImageDiv img {
    width: 80%;
    height: inherit;
  }
}

.jumpLinksDiv {
  position: absolute;
  left: 6vh;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  z-index: 10;
}

.jumpLinksDiv ol {
  grid-gap: 6vh;
  gap: 6vh;
  list-style: none;
}

.jumpLinksDiv ol li {
  counter-increment: item 1;
  font-family: Montserrat;
  cursor: pointer;
  transition: color 100ms ease-in;
  font-size: 3.25vh;
}

.jumpLinksDiv ol li a {
  text-decoration: none;
  color: var(--color-3);
  transition: color 100ms ease-in;
  text-shadow: 2px 2px 5px black;
}

.jumpLinksDiv ol li a:hover {
  color: var(--color-5);
}

.jumpLinksDiv ol li::before {
  content: "0" counter(item) ".";
  color: var(--color-5);
  margin-right: 0.825vh;
}

@media (max-width: 1050px) {
  .jumpLinksDiv {
    display: none;
  }
}

.socialLinksDiv {
  position: absolute;
  right: 2.4vh;
  grid-gap: 3vh;
  gap: 3vh;
  z-index: 10;
}

.socialLinksDiv h1 {
  position: relative;
  right: 13.5vh;
  color: var(--color-3);
  margin-top: 1.2vh;
  font-family: Montserrat;
  font-weight: 400;
  font-size: 2.8vh;
}

.socialLinksDiv h1::after {
  position: relative;
  left: 14.5vh;
  bottom: 1.7vh;
  content: "";
  display: block;
  width: 15vh;
  height: 0.4vh;
  background-color: var(--color-3);
}

.linkSVG {
  height: 5.3vh;
  width: 5.3vh;
  fill: var(--color-3);
  transition: all 100ms ease-in;
}

.linkSVG:hover {
  fill: var(--color-5);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

@media (max-width: 800px) {
  .socialLinksDiv {
    display: none;
  }
} 

.navbar {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 10vh;
    z-index: 10;
    align-items: center;
    transition: all 200ms ease-in;
}

.navbar img {
    height: 70%;
    position: relative;
    display: none;
}

.navbar__menu {
    margin-left: auto;
    fill: white;
    height: 70%;
    aspect-ratio: 1/1;
    display: none;
    align-items: center;
    justify-content: center;
    transition: all 200ms ease-in;
}

.navbar__menu.active {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    fill: var(--color-5);
}

.menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: white;
    z-index: 10;
    grid-gap: 20px;
    gap: 20px;
    -webkit-animation: fade-in 400ms ease-in;
            animation: fade-in 400ms ease-in;
}

@-webkit-keyframes fade-in {
    from {
        opacity: 0;
    } to {
        opacity: 0.98;
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
    } to {
        opacity: 0.98;
    }
}

@-webkit-keyframes fade-out {
    from {
        opacity: 0.98;
    } to {
        opacity: 0;
    }
}

@keyframes fade-out {
    from {
        opacity: 0.98;
    } to {
        opacity: 0;
    }
}

.menu__jumps {
    font-family: Montserrat;
    text-decoration: none;
    color: #444444;
    font-weight: 700;
    font-size: 40px;
}

.menu__socials {
    position: absolute;
    bottom: 10px;
    width: 80%;
    justify-content: space-around;
}

.menu__socials svg {
    height: 50px;
    width: 50px;
}

@media (max-width: 800px) {
    .navbar img {
        display: block;
    }

    .navbar__menu {
        display: flex;
    }

    .navbar {
        position: absolute;
        height: 55px;
        padding: 0px 10px;
        display: flex;
        align-items: center;
        box-sizing: border-box;
    }
}
#forthPage {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100vw;
}

.otherSkillsContainer {
  margin: 10vh 0vh;
  position: relative;
  width: 85%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.otherSkillsDiv {
  position: relative;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 75%;
  height: 400px;
  background-color: var(--color-2);
  overflow: hidden;
  opacity: 0;
  transition: all 500ms ease-in;
}

.otherSkillsTextDiv {
  font-family: Montserrat;
  padding: 30px;
  box-sizing: border-box;
  width: calc(100% - 400px);
  grid-gap: 20px;
  gap: 20px;
  height: 100%;
}

.otherSkillsTextDiv h1 {
  color: var(--color-4);
  font-size: 30px;
}

.otherSkillsTextDiv p {
  font-size: 23px;
  color: var(--color-3);
}

.otherSkillsDiv img {
  width: 400px;
  aspect-ratio: 1/1;
}

.otherSkillsDiv iframe {
  position: relative;
  top: -54px;
  width: 400px;
  height: 454px;
  margin-bottom: -54px;
}
.otherSkillsDiv.active {
  opacity: 1;
}

.otherSkillsDiv.active {
  left: 50% !important;
}

@media (max-width: 1200px) {
  .otherSkillsDiv {
    width: 100%;
  }
}

@media (max-width: 780px) {
  .otherSkillsContainer {
    width: 95%;
  }

  .otherSkillsDiv {
    flex-direction: column;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .otherSkillsTextDiv {
    order: 2;
    width: 100%;
    padding: 20px;
  }

  .otherSkillsDiv iframe {
    order: 1;
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
  }

  .otherSkillsDiv img {
    width: 100%;
  }
}

#secondPage {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100vw;
}

#projectsContainer {
  margin: 15vh 0vh;
  width: 85%;
}

.projectCards {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 90%;
  background-color: var(--color-2);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 3vh 0px;
  justify-content: center;
  transition: opacity 500ms ease-in, -webkit-transform 100ms ease-in;
  transition: transform 100ms ease-in, opacity 500ms ease-in;
  transition: transform 100ms ease-in, opacity 500ms ease-in, -webkit-transform 100ms ease-in;
  font-family: Montserrat;
  padding: 0px 1px;
}

.projectCards:hover {
  -webkit-transform: translate(-50%, -5px);
          transform: translate(-50%, -5px);
  box-shadow: 0px 0px 20px black;
}

.projectCards:hover h1 {
  color: var(--color-5);
}

.projectCards h1 {
  margin-top: 1.5vh;
  margin-left: 5%;
  color: var(--color-4);
  font-size: 4.8vh;
  transition: color 250ms ease-in;
}

.projectCards p {
  margin: 2.5vh 5%;
  color: var(--color-4);
  font-size: 3vh;
  width: 70%;
}

.projectSVG {
  position: absolute;
  right: 6vh;
  height: 90%;
  opacity: 0.2;
  z-index: -1;
}

@media (max-width: 800px) {
  #projectsContainer {
    margin: 10vh 0px;
    width: 95%;
  }

  .projectCards {
    margin: 2vh 0px;
    width: 100%;
  }

  .projectCards h1 {
    font-size: 25px;
  }

  .projectCards p {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 15px;
  }
}

#thirdPage {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100vw;
}

#skillsContainer {
  margin: 10vh 0vh;
  position: relative;
  width: 85%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.skillsProgressBarParentContainer {
  width: 100%;
  box-shadow: 0px 0px 20px black;
  margin: 2vh 0px;
  background-color: var(--color-2);
  font-family: Montserrat;
  padding: 1px 0px;
  align-items: center;
}

.skillsProgressBarParentContainer h1 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 6.5vh;
  margin: 2vh 0;
  color: var(--color-4);
}

.skillsProgressBarContainer {
  position: relative;
  width: 85%;
  grid-gap: 5%;
  gap: 5%;
  margin: 6vh 0 15vh 0;
}

.skillsProgressBarContainerChildren {
  width: 50%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
}

.actualProgressBarParentDiv {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
  margin: 0 0 5vh 0;
}

.actualProgressBarParentDiv h2 {
  color: var(--color-4);
  font-size: 3.5vh;
  margin: 0px 2.5vh;
  margin-bottom: 1vh;
}

.bar {
  width: 100%;
  height: 2vh;
  background-color: var(--color-1);
  border-radius: 1vh;
}

.progress {
  height: 2vh;
  background-color: var(--color-5);
  border-radius: 1vh;
  position: absolute;
  bottom: 0px;
  box-shadow: 0px 0px 4px var(--color-5);
}

.progressPercentageDiv {
  position: absolute;
  right: 0px;
  padding: 3px 5px;
  -webkit-transform: translate(1vh, -130%);
          transform: translate(1vh, -130%);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: var(--color-1);
  color: white;
  font-weight: bold;
}

.progressPercentageDiv::after {
  content: "";
  position: absolute;
  display: block;
  height: 1.5vh;
  bottom: -0.725vh;
  right: 1vh;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  aspect-ratio: 1;
  background-color: var(--color-1);
}

@media (max-width: 800px) {
  #skillsContainer {
    width: 95%;
  }

  .skillsProgressBarContainer {
    width: 95%;
    flex-direction: column;
    grid-gap: 0px;
    gap: 0px;
  }

  .skillsProgressBarContainerChildren {
    width: 100%;
  }
}

.work-container {
    margin: 0px auto;
    width: 80%;
}

.work-container__main {
    width: 80%;
    margin: 10vh auto;
}

.work-container__tabs {
    position: relative;
    flex-shrink: 0;
}

.work-container__tabs .MuiTabs-indicator {
    background-color: var(--color-5);
    transition: all 250ms linear;
    width: 4px;
}

.work-container__tabs__tab {
    color: white !important;
    font-family: Montserrat !important;
    font-weight: 700 !important;
    transition: all 150ms ease-in !important;
    padding: 1.7vh 2.5vh 1.7vh 1.7vh;
    font-size: 2.2vh !important;
    line-height: 2.2vh !important;
    align-items: flex-end !important;
}

.work-container__tabs__tab:hover {
    color: var(--color-5) !important;
}

.work-container__tabs__tab.active {
    color: var(--color-5) !important;
}

.work-container__content {
    color: white;
    font-family: Montserrat;
    padding: 0 1.5vh;
    padding-left: 2.5vh;
}

.work-container__content h1 {
    font-size: 5vh;
    color: var(--color-3);
}

.work-container__content p {
    font-size: 2.5vh;
    margin: 1vh 0;
    color: var(--color-4);
}

.work-container__content ul {
    list-style: none;
    margin-top: 3vh;
}

.work-container__content li {
    position: relative;
    padding: 1.5vh 0 1.5vh 3vh;
    font-size: 3vh;
}

.work-container__content li::before {
    content: '▹';
    color: var(--color-5);
    position: absolute;
    left: 0;
}

@media (max-width: 1050px) {
    .work-container {
        width: 95%;
    }

    .work-container__main {
        flex-direction: column;
        width: 100%;
        margin: 2vh auto;
    }

    .work-container__tabs {
        margin-bottom: 2vh !important;
    }

    .work-container__tabs__tab {
        text-align: center;
    }

    .work-container__tabs .MuiTabs-indicator {
        height: 4px;
    }

    .work-container__content {
        padding: 0;
        width: 95%;
        margin: 0 auto;
    }

    .work-container__content h1 {
        font-size: 3.5vh;
    }

    .work-container__content p {
        font-size: 1.75vh;
    }

    .work-container__content li {
        font-size: 2.2vh;
    }
}
